<template>
  <div>
    <div class="forgot-info">
      <div class="login-logo">
        <b class="logo"></b>
      </div>
      <div class="setting-forgot">
        <div class="return-login" v-if="step === 0" @click="back">返回登录 <i class="icon-font">&#xe603;</i></div>
        <div class="head">找回密码</div>
        <div v-if="step!==2">
          <div class="steps">
            <span class="big-circle"><span class="inner-circle"></span></span>
            <span class="line" :class="step==1?'light':''"></span>
            <span class="big-circle"><span class="inner-circle circle-last" :class="step==1?'light-circle':''"></span></span>
          </div>
          <div class="steps-warn">
            <span>验证手机号</span>
            <span :class="step===0? 'grey': ''">重置密码</span>
          </div>
        </div>
        <div v-if="step === 0">
          <input name="telephone" v-validate="{ rules: { regex: /^(13[0-9]|145|147|15[0-235-9]|170|176|177|18[0-9])[0-9]{8}$/} }" :class="{'is-danger': errors.has('telephone') }" placeholder="手机号码" class="telphone input-style" v-model="phoneNum" >
          <div class="v-code">
            <input name="vcode" v-validate="{ rules: { regex: /^.{4}$/} }" :class="{'is-danger': errors.has('vcode') }"  placeholder="校验码" class="code-input input-style" v-model="vCode">
            <img class="vcode" @click="refreshVcodeUrl" :src="vCodeUrl" alt="">
          </div>
          <div class="tel-code">
            <input name="phoneVcode" v-validate="{ rules: { regex: /^.{4}$/} }" :class="{'is-danger': errors.has('phoneVcode') }" placeholder="手机验证码" class="phone-code-input input-style" v-model="phoneVcode">
            <span @click="getPhoneVcode" v-show="stoped">获取验证码</span>
            <span v-show="!stoped">{{timer + '秒后可重新发送'}}</span>
          </div>
          <div class="error-info" v-show="error"><i class="icon-font icon-error">&#xe601;</i> 您输入的用户名或验证码错误</div>
          <template v-if="fields.fields.telephone">
            <div :class="{'is-ok': !errors.any()&&!fields.fields.telephone.clean&&!fields.fields.phoneVcode.clean&&!fields.fields.vcode.clean&&phoneNum&&vCode&&phoneVcode}"  class="jump-to" @click="next">找回密码</div>
          </template>
        </div>
        <div v-if="step === 1">
          <input name="password" v-validate="{ rules: { regex: /.{6,20}$/} }" :class="{'is-danger': errors.has('password') }" type="password"  placeholder="请输入新的密码" class="input-style" v-model="passwd" >
          <input name="password2" v-validate="{ rules: { regex: /.{6,20}$/} }" :class="{'is-danger': errors.has('password2') }" type="password"  placeholder="请再次输入密码" class="input-style confirm-input" v-model="passwdConfirm">
          <template v-if="fields.fields.password">
            <div :class="{'is-ok': !errors.any()&&!fields.fields.password2.clean&&!fields.fields.password.clean&&(passwd===passwdConfirm)&&passwd&&passwdConfirm}" class="jump-to" @click="setPasswd">找回密码</div>
          </template>
        </div>
        <div class="reLogin" v-if="step === 2">
          <div class="img-info">
            <img src="../../assets/img/login/icon.png" alt="">
            <p>新密码设置成功!</p>
          </div>
          <div class="reback" @click="back">去登录</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import LoginAPI from '@/api/loginAPI'
import '@/assets/js/security'

export default {
  name: 'forgot',
  data () {
    return {
      step: 0,
      /**
       * rsa publickey 初始化参数
       * @type {String}
       */
      rediskey: '',
      exponent: '',
      modulus: '',
      /**
       * 用户名 密码 验证码
       * @type {String}
       */
      phoneNum: '',
      passwd: '',
      passwdConfirm: '',
      vCode: '',
      vCodeUrl: process.env.DOMAIN + '/bshop/forget/captcha/get',
      phoneVcode: '',
      error: false,
      stoped: true,
      timer: 60
    }
  },
  components: {
  },
  mounted: function () {
    /**
     * 初始化公钥
     * @param  {[type]} res [description]
     * @return {[type]}     [description]
     */
    LoginAPI.getRSAPublicKey().then(res => {
      if (res.data.code === 0) {
        let callbackData = res.data.data
        this.rediskey = callbackData.rediskey
        this.exponent = callbackData.exponent
        this.modulus = callbackData.modulus
      } else {
        console.log('初始化公钥失败')
      }
    })
  },
  methods: {
    /**
     * 设置密码
     */
    setPasswd () {
      if (!(!this.errors.any() && !this.fields.fields.password2.clean && !this.fields.fields.password.clean && (this.passwd === this.passwdConfirm) && this.passwd && this.passwdConfirm)) {
        console.log('前端输入验证不通过，点击按钮无效！')
        return
      }
      let postObj = {
        'loginAccount': this.phoneNum,
        'code': this.phoneVcode,
        'password': this.passwd,
        'rPassword': this.passwdConfirm,
        'captcha': this.vCode
      }
      if (this.passwd !== this.passwdConfirm) {
        this.$message({
          message: '两次密码输入不相等',
          type: 'error'
        })
        return
      } else {
        LoginAPI.setNewPwd(postObj).then(res => {
          if (res.data.code === 0) {
            this.step = 2
          } else {
            this.$message({
              message: res.data.message,
              type: 'error'
            })
          }
        })
      }
    },
    /**
     * 获取手机验证码
     * @return {[type]} [description]
     */
    getPhoneVcode () {
      let postObj = {
        'type': 2,
        'mobile': this.phoneNum
      }
      LoginAPI.getPhoneVcode(postObj).then((res) => {
        if (res.data.code === 0) {
          this.$message({
            message: res.data.message,
            type: 'success'
          })
          this.stoped = false
          this.timer = 60
          let time = setInterval(() => {
            this.timer--
            if (this.timer < 1) {
              clearInterval(time)
              this.stoped = true
            }
          }, 1000)
        } else {
          this.$message({
            message: res.data.message,
            type: 'error'
          })
        }
      })
    },
    /**
     * 下一步按钮，进行短信验证，进入重置密码界面
     * @return {Function} [description]
     */
    next () {
      if (!(!this.errors.any() && !this.fields.fields.telephone.clean && !this.fields.fields.phoneVcode.clean && !this.fields.fields.vcode.clean && this.phoneNum && this.vCode && this.phoneVcode)) {
        console.log('前端输入验证不通过，点击按钮无效！')
        return
      }
      let postObj = {
        'loginAccount': this.phoneNum,
        'code': this.phoneVcode,
        'captcha': this.vCode
      }
      console.log(postObj)
      LoginAPI.validatePhoneVcode(postObj).then((res) => {
        if (res.data.code === 0) {
          this.step = 1
        } else {
//          this.$message({
//            message: res.data.message,
//            type: 'error'
//          })
          this.error = true
        }
      })
    },
    /**
     * 刷新验证码
     * @return {[type]} [description]
     */
    refreshVcodeUrl () {
      this.vCodeUrl = process.env.DOMAIN + '/bshop/forget/captcha/get?v=' + new Date().getTime()
    },
    /**
     * rea加密
     * @param  {[type]} text [description]
     * @return {[type]}      [description]
     */
    encryptRSA (text) {
      let key = window.RSAUtils.getKeyPair(this.exponent, '', this.modulus)
      return window.RSAUtils.encryptedString(key, text)
    },
    /**
     * 前往登录页面
     * @return {[type]} [description]
     */
    back () {
      this.$router.push({ path: '/login' })
    }
  }
}
</script>

<style lang="less" rel="stylesheet/less" >
  .forgot-info{
    margin-top: 65px;
    width: 100%;
    height: 100%;
    .login-logo{
      text-align: center;
      margin-bottom: 40px;
      .logo{
        display: inline-block;
        width: 396px;
        height: 93px;
        background: url("../../assets/img/login/logo-small.png") no-repeat;
      }
    }
    .setting-forgot{
      width:398px;
      height: 534px;
      background: #FFFFFF;
      border: 1px solid #E8E8E8;
      border-radius: 4px;
      margin: 0 auto;
      padding: 24px 40px 0 40px;
      cursor: pointer;
      .return-login{
        float: right;
        font-family: MicrosoftYaHei;
        font-size: 14px;
        color: #7C98B6;
        padding-left: 5px;
        border-left:2px solid #ccc;
      }
      .head{
        font-family: MicrosoftYaHei;
        font-size: 18px;
        color: #333333;
        letter-spacing: 0;
        margin-top: 24px;
        margin-bottom: 40px;
        text-align: center;
      }
      .steps{
        padding: 0 55px;
        .big-circle{
          display: inline-block;
          width:22px;
          height: 22px;
          border-radius: 50%;
          background: #FFFFFF;
          box-shadow: 0 0 8px 0 rgba(6,134,249,0.15);
          .inner-circle{
            background: #5488F9;
            display: inline-block;
            width: 14px;
            height: 14px;
            border-radius: 50%;
            margin-left: 4px;
            vertical-align: middle;
          }
          .circle-last{
            background: #D5DFE4;
          }
          .light-circle{
            background: #5488F9;
            display: inline-block;
            width: 14px;
            height: 14px;
            border-radius: 50%;
            margin-left: 4px;
            vertical-align: middle;
          }
        }
        .line{
          background: #D5DFE4;
          width: 231px;
          height: 2px;
          display: inline-block;
        }
        .light{
          background: #5488F9;
          width: 231px;
          height: 2px;
          display: inline-block;
        }
      }
      .steps-warn{
        font-family: MicrosoftYaHei;
        font-size: 13px;
        color: #5488F9;
        margin-top: 10px;
        margin-bottom: 40px;
        span:first-child{
          padding-left: 27px;
        }
        span:last-child{
          padding-left: 207px;
        }
        .grey {
          color:#B0BEC5;
        }
      }
     .input-style {
        width: 400px;
        height: 56px;
        background: #F5F8FA;
        border-radius: 4px;
        margin: 0 auto;
        font-size: 14px;
        color: #7C98B6;
        border: none;
        padding-left:16px;
        border:1px solid #F5F8FA;
        box-sizing: border-box;
      }
      .input-style:focus{
        border:1px solid #02AAAA;
        outline: none;
      }
      .confirm-input {
        margin-top:16px;
      }
      .is-danger{
          border:1px solid #FF3B6E
      }
      .v-code{
        margin-top:16px;
        position: relative;
        .vcode{
          position: absolute;
          top: 0;
          height: 56px;
          width: 120px;
          right: -2px;
        }
      }
      .tel-code{
        margin-top:16px;
        position: relative;
        span{
          position: absolute;
          background: #5488F9;
          border-radius: 0 4px 4px 0;
          width: 120px;
          height: 56px;
          right: 0;
          top: 0;
          /* vertical-align: middle; */
          text-align: center;
          line-height: 56px;
          color: #fff;
        }
      }
        .error-info{
          width:366px;
          height: 24px;
          background: #FF3B6E;
          background: rgba(255,255,255,0.90);
          border: 1px solid #FF3B6E;
          /*border: 1px solid rgba(255,255,255,0.80);*/
          border-radius: 4px;
          margin-top: 24px;
          font-family: MicrosoftYaHei;
          font-size: 14px;
          color: #FF3B6E;
          padding: 15px 16px;
          line-height: 24px;
          .icon-error{
            color: #FF3B6E;
          }
      }
      .jump-to{
        width:400px;
        height: 56px;
        line-height: 56px;
        margin: 16px auto 8px;
        background: #EAF0F6;
        border: 1px solid #DFE3EB;
        border-radius: 4px;
        font-family: MicrosoftYaHei;
        font-size: 18px;
        color: #B0C1D4;
        text-align: center;
        cursor: pointer;
      }
     .jump-to.is-ok{
        background: #5488F9;
        color: #fff;
      }
      .reLogin{
        .img-info{
          img{
            margin-top: 90px;
            padding-left: 99px;
            margin-bottom: 15px;
          }
          p{
            text-align: center;
            font-family: MicrosoftYaHei;
            font-size: 14px;
            color: #666666;
            margin-bottom: 79px;
          }
        }
        .reback{
          width:120px;
          height: 34px;
          background: #5488F9;
          border-radius: 4px;
          color: #fff;
          font-family: MicrosoftYaHei;
          font-size: 14px;
          text-align: center;
          line-height: 34px;
          cursor: pointer;
          margin: 0 auto;
        }
      }
    }
  }
  .login-wraper {
    width:350px;
    margin:200px auto;
    h3{
      text-align: center;
    }
  }
</style>



